import React from 'react';
import Events from '../Events';

export default class Main extends React.Component {

  RenderTable = (Title) => {

    switch(true) {
      default:
        return <div>
          <h1>An error has occured.</h1>
        </div>
    }
  }

  render() {
    const {
      Oauth2,
      Cerner,
      MainArray,
      MainIndex,
      SetAppState
    } = this.props;

    return Cerner ? (
      <div className="App-Main">
        <ul className="Main-Nav">
          {MainArray.map((item, i) => {
            if (MainIndex === i) return <li key={i} 
              className="Main-Nav-Link Main-Nav-Link-Active" 
              onClick={() => {
                Events.client.request(Oauth2, SetAppState, MainArray[MainIndex]);
                SetAppState({ Loading: true, MainIndex: i });
              }}
            >{item}</li>
            return <li key={i} 
              className="Main-Nav-Link" 
              onClick={() => {
                Events.client.request(Oauth2, SetAppState, MainArray[MainIndex]);
                SetAppState({ Loading: true, MainIndex: i })
              }}
            >{item}</li>
          })}
        </ul>
        {this.RenderTable(MainArray[MainIndex], Cerner)}
      </div>
    ) : (
      <div className="App-Main">
        <h1>Please select account type to Log-In</h1>
      </div>
    )
  }
}